.side-nav{
    display: flex;
    flex-direction: column;
    width: 20vw;
}
.side-align{
    align-content: center;
}
.side-nav-end{
    justify-content: flex-end;
    width: 100%;
    height: 100%;
}
.side-nav-top{
    justify-content: flex-start;
    width: 100%;
    height: 100%;
}
.side-nav-center{
    justify-content: center;
    width: 100%;
    height: 100%;
}
.nav-list{
    display: flex;
    flex-direction: column;
}
.nav-image{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.img-circle{
    border-style: solid;
    border-radius: 50%;
    border-width: 1px;
    border-color:  rgba(255,255,255,0.1);
    width: 200px;
    height: 200px;
    margin-top: 25px;
}
.footer{
    border-top-style: solid;
    border-top-color:  rgba(255,255,255,0.1);
    border-width: 1px;
    padding: 15px;
}
.sbg-dark{
    background-color: #282c49;
    border-right-style: solid;
    border-right-color:  rgba(255,255,255,0.1);
    border-width: 1px;
    color: white;
}